import React from 'react'
import './Footer.css'
import { Button } from '../Components/Button';
import { Link } from 'react-router-dom';

function Footer() {
    return (
        <div className='footer-container'>
        <section className='footer-subscription'>
            <p className='footer-subscrption-heading'>
                Developer: Kianush Abedi
            </p>
            <p className='footer-subscription-text'>
                Please enter your Email
            </p>
            <div className='input-areas'>
                <form>
                    <input type='email' name='email' placeholder='abedikianush@gmail.com' className='footer-input'/>
                    <Button buttonStyle='btn--outline btn-footer' >Send</Button>
                </form>
            </div>
        </section>
        <div className='footer-links'>
            <div className='footer-link-wrapper'>
                <div className='footer-link-items'>
                    <h2>About Us</h2>
                    <Link to='/'>How it works</Link>
                    <Link to='/'>Testimonials</Link>
                    <Link to='/'>Careeres</Link>
                    <Link to='/'>Investors</Link>
                </div>
                <div className='footer-link-items'>
                    <h2>Contact Us</h2>
                    <Link to='/'>Contact</Link>
                    <Link to='/'>Support</Link>
                    <Link to='/'>Destinations</Link>
                    <Link to='/'>Spossorships</Link>
                </div>
            </div>
            <div className='footer-link-wrapper'>
                <div className='footer-link-items'>
                    <h2>Social Media</h2>
                    <Link to='/'>Instagram</Link>
                    <Link to='/'>Facebook</Link>
                    <Link to='/'>Youtube</Link>
                    <Link to='/'>Twitter</Link>
                </div>
            </div>
        </div>
        <section className='social-media'>
            <div className='social-media-wrap'>
                <div className='footer-logo'>
                <Link to='/' className='navbar-logo'>
                 ReactSite<i class="fab fa-react"></i>
                </Link>
                </div>
                <small className='website-rights'>abedikianush@gmail.com © 2021</small>
                <div className='social-icons'>
                    <Link className='social-icon-link facebook'
                    to='/'
                    target='_blank'
                    aria-label='Facebook'>
                        <i className='fab fa-facebook-f'/>
                    </Link>
                    <Link className='social-icon-link instagram'
                    to='https://instagram.com/kianush.abedi/'
                    target='_blank'
                    aria-label='Instagram'>
                        <i className='fab fa-instagram'/>
                    </Link>
                    <Link className='social-icon-link youtube'
                    to='/'
                    target='_blank'
                    aria-label='Youtube'>
                        <i className='fab fa-youtube'/>
                    </Link>
                    <Link className='social-icon-link twitter'
                    to='/'
                    target='_blank'
                    aria-label='Twitter'>
                        <i className='fab fa-twitter'/>
                    </Link>
                    <Link className='social-icon-link linkedin'
                    to='/'
                    target='_blank'
                    aria-label='LinkedIn'>
                        <i className='fab fa-linkedin'/>
                    </Link>

                </div>
            </div>
        </section>
    </div>
    )
}

export default Footer
